import { useState, useReducer } from 'react';

const useLoading = (initValue?: boolean) => {
  const [loading, setLoading] = useState(!!initValue);
  const deco = (func: Function) => async (...args: any[]) => {
    setLoading(true);
    const res = await func(...args);
    setLoading(false);
    return res;
  };
  return { loading, deco };
};

const loadingUpdator = (
  current: AnObject,
  {
    type,
    id,
    value,
  }: { type: 'add' | 'remove'; id: string | number; value: any }
) => ({
  ...current,
  [id]: type === 'add' ? value : false,
});

const useTableLoading = () => {
  const [tableLoadingItems, setLoadingItems] = useReducer(loadingUpdator, []);
  const tableDeco = (func: Function) => async (
    firstArg:
      | {
        id: string | number;
        value?: any;
      }
      | string
      | number,
    ...args: any[]
  ) => {
    const { id, value = true } =
      typeof firstArg === 'object' ? firstArg : { id: firstArg };
    setLoadingItems({ type: 'add', id, value });
    const res = await func(id, ...args);
    setLoadingItems({ type: 'remove', id, value });
    return res;
  };
  return { tableLoadingItems, tableDeco };
};

useLoading.useTableLoading = useTableLoading;

export default useLoading;
