/**
 * 页面layout模板
 */
import React, { useMemo, Suspense, useCallback } from 'react';
import classnames from '../utils/classnames';
import {
  BrowserRouter,
  HashRouter,
  Switch,
  Route,
  Redirect,
  useLocation,
} from 'react-router-dom';
import {
  Routes,
  ChildrenRoutes,
  CustomRouterProps,
  ContentProps,
  LayoutProps,
  BasicLayoutProps,
  ListLayoutProps,
  DashboardLayoutProps,
} from './interface';
import { Sidebar, PageError, PageLoading } from '@zenlayer/zen-ui';
import warning from '../utils/warning';

const Content: React.FC<ContentProps> = ({
  title,
  routes = [],
  footer,
  contentStyle,
  sidebarStyle,
}) => {
  const location = useLocation();
  const sidebarBlackList = useMemo(() => {
    return routes.reduce<string[]>((r, c) => {
      return c.withoutSidebar && c.path ? [...r, c.path] : r;
    }, []);
  }, []);
  const withSidebar = sidebarBlackList.indexOf(location.pathname) < 0;

  const footerBlackList = useMemo(() => {
    return routes.reduce<string[]>((r, c) => {
      return c.withoutFooter && c.path ? [...r, c.path] : r;
    }, []);
  }, []);
  const withFooter = footerBlackList.indexOf(location.pathname) < 0;
  const initRoutesFn = useCallback(
    (_p?: ChildrenRoutes[]) => {
      if (_p) {
        return _p.reduce((r, c) => {
          const { path, name, children } = c;
          if (children) {
            r = [...r, c, ...initRoutesFn(children)];
          }
          r = [...r, c];
          return r;
        }, [] as ChildrenRoutes[]);
      }
      return [];
    },
    [routes]
  );
  const initRoutes = initRoutesFn(routes) || [];
  return (
    <div
      className={classnames('zen-layout-content', {
        //'sidebar-space': !!withSidebar,
      })}
      style={contentStyle}
    >
      {!!withSidebar && (
        <Sidebar title={title} links={routes} style={sidebarStyle} />
      )}
      <div className="zen-layout-content-main">
        <div className="zen-layout-content-main-route">
          <Suspense fallback={<PageLoading.LoadingPane text="Loading..." />}>
            <Switch>
              {initRoutes.map(({ path, component }) => {
                return (
                  path &&
                  component && (
                    <Route
                      key={path}
                      path={path}
                      component={component}
                      exact={true}
                    />
                  )
                );
              })}
              {initRoutes.length > 0 && (
                <Redirect to={initRoutes[0].path || ''} />
              )}
              <Route component={PageError} />
            </Switch>
          </Suspense>
        </div>
        {!!withFooter && footer}
      </div>
    </div>
  );
};

const CustomRouter = ({ children, routerType }: CustomRouterProps) => {
  if (routerType === 'BrowserRouter') {
    return <BrowserRouter>{children}</BrowserRouter>;
  }
  if (routerType === 'HashRouter') {
    return <HashRouter>{children}</HashRouter>;
  }
  return children || null;
};

function Layout({
  header,
  routes,
  footer,
  routerType,
  contentStyle,
  sidebarStyle,
  style,
}: LayoutProps) {
  //根据isAuthenticated过滤菜单
  const filterAuth = useCallback(
    (routes: Routes[]): Routes[] => {
      return routes
        .map((route) => {
          if (route.isAuthenticated === false) {
            return undefined;
          }

          if (route.children && Array.isArray(route.children)) {
            return {
              ...route,
              children: filterAuth(route.children as Routes[]),
            };
          }
          return route;
        })
        .filter((i) => !!i) as Routes[];
    },
    [routes]
  );
  const hasAuthRoutes: Routes[] = filterAuth(routes) || [];

  return (
    <div className="zen-layout" style={style}>
      {header}
      <CustomRouter routerType={routerType}>
        <Switch>
          {hasAuthRoutes.map(({ path, name, children }) => {
            return (
              <Route
                key={path}
                path={path}
                render={() => (
                  <Content
                    title={name}
                    routes={children}
                    footer={footer}
                    contentStyle={contentStyle}
                    sidebarStyle={sidebarStyle}
                  />
                )}
              />
            );
          })}
          {hasAuthRoutes.length > 0 && <Redirect to={hasAuthRoutes[0].path} />}
          <Route component={PageError} />
        </Switch>
      </CustomRouter>
    </div>
  );
}

