/**
 * 基础layout模板
 */
export function BasicLayout({ children, className, style }: BasicLayoutProps) {
  return (
    <div className={classnames('basic-layout', className)} style={style}>
      {children}
    </div>
  );
}

/**
 * 列表页面layout
 */
export function ListLayout({
  name,
  children,
  afterHeader,
  search,
  actions,
  pagination,
  className,
  style,
}: ListLayoutProps) {
  return (
    <BasicLayout className={classnames('list-layout', className)} style={style}>
      {name && (
        <div className="header mb-4">
          <h2 className="title">{name}</h2>
        </div>
      )}
      {afterHeader}
      {(search || actions) && (
        <div className="mb-3">
          <div className="handle">
            <div className="handle__l">{search}</div>
            <div className="handle__r">{actions}</div>
          </div>
        </div>
      )}
      <div>
        {children}
        {pagination && <div className="pagination mt-2">{pagination}</div>}
      </div>
    </BasicLayout>
  );
}

export function DashboardLayout({
  children,
  footer,
  fixbar,
  fixbarHeight = 90,
  className,
  style,
}: DashboardLayoutProps) {
  return (
    <div
      className="dashboard-layout"
      style={{ paddingBottom: fixbar ? fixbarHeight : 'auto' }}
    >
      <div className="dashboard-layout__bg"></div>
      <div
        className={classnames('dashboard-layout__main', className)}
        style={style}
      >
        {children}
      </div>
      {footer}
      {fixbar && (
        <div
          className="dashboard-layout__fixbar"
          style={{ height: fixbarHeight }}
        >
          <div className="dashboard-layout__fixbar__inner">{fixbar}</div>
        </div>
      )}
    </div>
  );
}

Layout.BasicLayout = BasicLayout;
Layout.ListLayout = ListLayout;
Layout.DashboardLayout = DashboardLayout;

export default Layout;
