import React from 'react';
import { IconProps } from './interface';
import classNames from '../utils/classnames';
import svgBaseProps from './utils/svgBaseProps';
import warning from '../utils/warning';

const Icon: React.FC<IconProps> = (props) => {
  const { type, size, className, spin, rotate, style, ...otherProps } = props;
  warning(!!type, '[zen-ui/icon] The “type” parameter must have');
  const svg = (type || '')
    .split('/')
    .filter((p: string) => !!p)
    .pop();
  return (
    <svg
      {...svgBaseProps}
      className={classNames(className, {
        'zen-ui-icon-anticon-spin': !!spin || svg === 'LoadingOutlined',
      })}
      style={{
        ...style,
        ...(rotate
          ? {
              msTransform: `rotate(${rotate}deg)`,
              transform: `rotate(${rotate}deg)`,
            }
          : {}),
      }}
      {...otherProps}
    >
      <use xlinkHref={`#${svg}`} />
    </svg>
  );
};
export default Icon;
