/**
 * className合并工具
 */
type JudgeFn = () => boolean;
type JudgeObj = { [key: string]: boolean | JudgeFn };
type ClassName = string | JudgeObj | (string | JudgeObj)[] | undefined;

const format = (obj: JudgeObj) => {
  const arr: string[] = [];
  Object.keys(obj).forEach((key: string) => {
    if (typeof obj[key] === 'boolean' && obj[key]) {
      arr.push(key);
    }
    if (typeof obj[key] === 'function') {
      const func = obj[key] as JudgeFn;
      func() && arr.push(key);
    }
  });
  return arr;
};

const classnames = (...args: ClassName[]) => {
  const classes = args.reduce((acc: string[], cur: ClassName) => {
    if (typeof cur === 'string' && cur) {
      acc.push(cur);
    }
    if (Array.isArray(cur)) {
      cur.forEach(item => {
        if (typeof item === 'string' && item) {
          acc.push(item);
        }
        if (typeof item === 'object') {
          acc = acc.concat(format(item));
        }
      });
    }
    if (typeof cur === 'object') {
      acc = acc.concat(format(cur as JudgeObj));
    }
    return acc;
  }, []);
  return classes.join(' ');
};

export default classnames;
