/**
 * 输入框
 */
import React, { useState, useRef, useEffect } from 'react';
import classnames from '../utils/classnames';
import { Icon } from '@zenlayer/zen-ui';
import { Tooltip } from 'antd';
import { InputProps } from './interface';

export default function Input({
  style,
  type,
  value = '',
  onChange,
  name,
  width = '100%',
  disabled = false,
  readonly = false,
  allowClear = false,
  onBlur,
  error,
  size,
  onFocus,
  className,
  onKeyDown,
  suffix,
}: InputProps) {
  const inputEl = useRef<HTMLInputElement>(null);
  const [shrink, setShrink] = useState<boolean>(false);
  const [isFocus, setFocus] = useState<boolean>(false);
  const canDoSomething = !disabled && !readonly;
  useEffect(() => {
    if (value !== '') {
      setShrink(true);
    }
    if (error && !shrink) {
      setShrink(true);
    }
  }, [value, error]);
  const handleInput = (v: string) => {
    onChange && onChange(v);
  };
  return (
    <div
      className={classnames('zen-input', className, {
        'zen-input__error': !!error,
        'zen-input__disabled': disabled,
        'zen-input__l': size === 'l',
      })}
      onClick={() => {
        if (canDoSomething) {
          (inputEl.current as HTMLInputElement).focus();
        }
      }}
      style={{ width, ...style }}
    >
      <div
        className={classnames('zen-input-label', 'zen-input-label__animated', {
          'zen-input-label__shrink': shrink,
          'zen-input-label__disabled': disabled,
          'zen-input-label__readonly': readonly,
          'zen-input-label__error': !!error,
          'zen-input-label__l': size === 'l',
          'zen-input-label__shrink__l': size === 'l' && shrink,
        })}
      >
        {name}
      </div>
      <Tooltip placement="top" title={error} visible={isFocus && !!error}>
        <input
          className={classnames('zen-input-content', {
            'zen-input-content__l': size === 'l',
          })}
          value={value}
          onChange={(e) => {
            handleInput(e.target.value);
          }}
          type={type}
          onKeyDown={onKeyDown}
          ref={inputEl}
          onBlur={() => {
            setFocus(false);
            if (onBlur) {
              onBlur();
            }
            setShrink(value !== '' || !!value || !!error);
          }}
          onFocus={() => {
            setFocus(true);
            if (onFocus) {
              onFocus();
            }
            canDoSomething && setShrink(true);
          }}
          disabled={disabled}
          readOnly={readonly}
          autoComplete="new-password"
        />
      </Tooltip>
      {canDoSomething && allowClear && value && (
        <Icon
          type="CloseCircleFilled"
          className="icon-clear"
          onClick={(e: any) => {
            e.stopPropagation();
            handleInput('');
            setShrink(false);
          }}
        />
      )}
      {suffix && suffix(value)}
    </div>
  );
}
