/**
 * 警告提示
 */
import React, { useState, useEffect } from 'react';
import { Icon } from '@zenlayer/zen-ui';
import classnames from '../utils/classnames';
import { AlertProps } from './interface';
const transitionTime = 200;

const iconType = {
  info: <Icon type="InfoCircleOutlined" className="icon-type" />,
  success: <Icon type="CheckCircleOutlined" className="icon-type" />,
  warning: <Icon type="InfoCircleOutlined" className="icon-type" />,
  error: <Icon type="CloseCircleOutlined" className="icon-type" />,
};

const Alert = ({
  show = true,
  type = 'warning',
  icon,
  children,
  closable,
  onClose,
  className,
  style,
}: AlertProps) => {
  const [visible, setVisible] = useState<boolean>(show);
  const [opacity, setOpacity] = useState<boolean>(false);
  const handleClose = () => {
    setOpacity(false);
    setTimeout(() => {
      setVisible(false);
      onClose && onClose();
    }, transitionTime);
  };
  const renderIcon = () => {
    if (icon === false) {
      return null;
    }
    return icon || iconType[type];
  };
  useEffect(() => {
    if (show) {
      setVisible(true);
      setTimeout(() => {
        setOpacity(true);
      }, 0);
    }
  }, [show]);
  return visible ? (
    <div
      className={classnames('zen-alert', `zen-alert--${type}`, className, {
        'zen-alert--show': opacity,
      })}
      style={{
        transition: `opacity ${transitionTime / 1000}s ease-in-out`,
        ...style,
      }}
    >
      {renderIcon()}
      {closable && (
        <Icon
          type="CloseOutlined"
          className="icon-close"
          onClick={handleClose}
        />
      )}
      {children}
    </div>
  ) : null;
};

export default Alert;
