/**
 * 头部组件
 */
import * as React from 'react';
import Logo from './logo';
import { HeaderProps } from './interface';

const Header = ({ left, right, onClickLogo, style }: HeaderProps) => {
  return (
    <div className="zen-header" style={style}>
      <div className="zen-header__l">
        <div className="logo">
          <div
            style={{
              width: '100%',
              height: '100%',
              padding: '8px',
              boxSizing: 'border-box',
            }}
          >
            <span
              style={onClickLogo ? { cursor: 'pointer' } : {}}
              onClick={() => {
                if (onClickLogo) {
                  onClickLogo();
                }
              }}
            >
              <Logo />
            </span>
          </div>
        </div>
        {left}
      </div>
      {right}
    </div>
  );
};
export default Header;
