import React from 'react';
import { Icon } from '@zenlayer/zen-ui';
import classnames from '../utils/classnames';
import { ButtonProps } from './interface';
import warning from '../utils/warning';

const Button = ({
  type = 'primary',
  size = 'm',
  icon,
  children,
  onClick,
  width,
  loading = false,
  disabled = false,
  htmlType,
  className,
  style,
  ...rest
}: ButtonProps) => {
  warning(
    React.isValidElement(icon) || !icon,
    '[zen-ui/button]: The icon attribute must be React.ReactElement'
  );
  return (
    <button
      type={htmlType}
      onClick={(e) => !loading && onClick && onClick(e)}
      className={classnames(`button button-${type} button-${size}`, className, {
        loading,
      })}
      style={{ ...style, width }}
      disabled={disabled}
      {...rest}
    >
      <span className="button__inner">
        {loading && (
          <Icon
            type="LoadingOutlined"
            className="button-icon"
            style={{
              marginRight: children ? 8 : 0,
            }}
          />
        )}
        {icon &&
          typeof icon !== 'string' &&
          React.cloneElement(icon, {
            className: 'button-icon',
            style: {
              marginRight: children ? 8 : 0,
            },
          })}

        {children && <span className="button-text">{children}</span>}
      </span>
    </button>
  );
};

export default Button;
