import * as React from 'react';
import classNames from '../utils/classnames';
import toArray from '../utils/childrenToArray';
import { SpaceProps } from './interface';

const spaceSize = {
  l: 8,
  ll: 16,
  lll: 24,
  llll: 32,
  lllll: 40,
};

const Space: React.FC<SpaceProps> = (props) => {
  const {
    size = 'l',
    align,
    className,
    children,
    direction = 'horizontal',
    ...otherProps
  } = props;

  const items = toArray(children);
  const len = items.length;

  if (len === 0) {
    return null;
  }

  const mergedAlign =
    align === undefined && direction === 'horizontal' ? 'center' : align;
  const prefixCls = 'zen-space';
  const cn = classNames(
    prefixCls,
    `${prefixCls}-${direction}`,
    {
      [`${prefixCls}-align-${mergedAlign}`]: !!mergedAlign,
    },
    className
  );

  const itemClassName = `${prefixCls}-item`;

  return (
    <div className={cn} {...otherProps}>
      {items.map((child, i) => (
        <div
          className={itemClassName}
          key={`${itemClassName}-${i}`}
          style={
            i === len - 1
              ? {}
              : {
                  [direction === 'vertical' ? 'marginBottom' : 'marginRight']:
                    typeof size === 'string' ? spaceSize[size] : size,
                }
          }
        >
          {child}
        </div>
      ))}
    </div>
  );
};

export default Space;
