import React from 'react';
import classnames from '../utils/classnames';
import { Icon } from '@zenlayer/zen-ui';
import { FooterProps } from './interface';

const links: { name: string; href: string; target: string }[] = [
  {
    name: 'Privacy',
    href: 'https://www.zenlayer.com/privacy-policy/',
    target: '_blank',
  },
  {
    name: 'Policy',
    href: 'https://www.zenlayer.com/cookie-policy/',
    target: '_blank',
  },
  {
    name: 'Terms',
    href: 'https://www.zenlayer.com/terms-of-use/',
    target: '_blank',
  },
];

const Footer = ({ className, style }: FooterProps) => {
  return (
    <footer className={classnames('zen-footer', className)} style={style}>
      {links.map((link) => (
        <a
          key={link.href}
          href={link.href}
          target={link.target}
          className="zen-footer-item"
        >
          {link.name}
        </a>
      ))}
      <span className="zen-footer-item">
        Copyright
        <Icon type="CopyrightOutlined" style={{ margin: '0 5px' }} />
        {new Date().getFullYear()} Zenlayer
      </span>
    </footer>
  );
};

export default Footer;
