/**
 * 侧边栏
 */
import * as React from 'react';
import { Link, useLocation } from 'react-router-dom';
import classnames from '../utils/classnames';
import { Icon } from '@zenlayer/zen-ui';
import { TheRoute, SidebarProps, MenuProps } from './interface';

const Menu = ({
  name,
  path,
  hideInMenu,
  children,
  isChildren = false,
}: MenuProps) => {
  const location = useLocation();
  const [showChildren, setShowChildren] = React.useState<boolean>(false);
  const handelShowChildren = () => {
    setShowChildren(!showChildren);
  };
  //命中二级菜单的话，自动展开
  React.useEffect(() => {
    children &&
      children.some(({ path }) =>
        new RegExp(`^${path}$`).test(location.pathname)
      ) &&
      setShowChildren(true);
  }, []);
  const active = new RegExp(`^${path}`).test(location.pathname);
  const HidChildrenDom = (
    <div
      className={classnames('zen-sidebar-item', {
        'zen-sidebar-item-active': active,
      })}
    >
      <div>
        {name}
        <div style={{ float: 'right', marginRight: '14px' }}>
          {showChildren ? (
            <Icon type="UpOutlined" />
          ) : (
            <Icon type="DownOutlined" />
          )}
        </div>
      </div>
    </div>
  );
  return !hideInMenu ? (
    children ? (
      <div>
        {path ? (
          <Link to={path as string} key={path} onClick={handelShowChildren}>
            {HidChildrenDom}
          </Link>
        ) : (
          <a onClick={handelShowChildren}>{HidChildrenDom}</a>
        )}
        {showChildren && (
          <div className="zen-sidebar-item">
            {children.map(({ name, path, hideInMenu, children, component }) => {
              return (
                <Menu
                  key={path}
                  name={name}
                  path={path}
                  hideInMenu={hideInMenu}
                  children={children}
                  isChildren={true}
                />
              );
            })}
          </div>
        )}
      </div>
    ) : (
      <Link to={path as string} key={path}>
        <div
          className={classnames('zen-sidebar-item', {
            'zen-sidebar-item-active': active,
            'zen-sidebar-childrenItem': isChildren,
            'zen-sidebar-childrenItem-active': isChildren && active,
          })}
        >
          <div>{name}</div>
        </div>
      </Link>
    )
  ) : null;
};

const Sidebar = ({ title, links, style }: SidebarProps) => {
  return (
    <div className="zen-sidebar" style={style}>
      <div className="zen-sidebar-title">{title}</div>
      {links.map(({ name, path, hideInMenu, children, component }) => {
        return (
          <Menu
            key={path || name}
            name={name}
            path={path}
            hideInMenu={hideInMenu}
            children={children}
          />
        );
      })}
    </div>
  );
};
export default Sidebar;
