/**
 * 加载中显示组件
 */
import React, { useEffect } from 'react';
import useLoading from '../hook/useLoading';
import classnames from '../utils/classnames';
import { Icon } from '@zenlayer/zen-ui';
import { LoadingPaneProps, LoadingProps } from './interface';

const LoadingPane = ({
  width = '100%',
  height = '100%',
  minHeight = 400,
  className,
  style,
  text,
  ...rest
}: LoadingPaneProps) => (
  <div
    className={classnames('loading-pane', className)}
    style={{ width, height, minHeight, ...style }}
    {...rest}
  >
    <div className="loading-pane__inner">
      <Icon type="LoadingOutlined" className="icon" />
      <br />
      {text}
    </div>
  </div>
);

const Loading = ({ init, dep = [], children, noData }: LoadingProps) => {
  const { loading, deco } = useLoading(true);
  const func = deco(init);
  useEffect(() => {
    func();
  }, dep);
  return <>{loading ? <LoadingPane /> : noData || children}</>;
};

Loading.LoadingPane = LoadingPane;

export default Loading;
